.about-me {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 500px;
  }
  
  .logo-container {
    font-size: 2rem;
    margin-bottom: 40px;
  }
  
  .text-container {
    text-align: center;
  }
  
  .text-container h1 {
    text-decoration: underline;
    font-size: 2.25rem;
    margin-bottom: 20px;
  }
  
  .text-container p {
    font-size: 20px;
    line-height: 1.5;
  }
  
  .description {
    margin: auto;
    padding: 50px 0;
  }
  
  .description {
    display: flex;
    flex-direction: column;
  }
  
  .description h2 {
    font-size: 36px;
    margin-bottom: 20px;
  }
  
  .container {
    margin: auto;
  }
  
  .me p {
    font-size: 20px;
    line-height: 1.5;
  }
  
  .me img {
    margin-bottom: 20px;
    margin-top: 50px;
  }
  
  .box-2 {
    margin-top: 50px;
  }
  
  .col {
    font-size: 1.25em;
    line-height: 1.5em;
  }
  
  .col ol {
    padding-left: 50px;
  }
  
  .col ol li {
    margin-bottom: 15px;
  }  